const keys    = require('./keys'),
      accents = require('./accents');

module.exports = function glob(value) {
  const chars = {
    a: "[Aa]",
    e: "[Ee]",
    i: "[Ii]",
    o: "[Oo]",
    u: "[Uu]",
    c: "[Cc]"
  };
  value = accents(value);
  const charKeys = keys(chars);
  return '*' + value.split('').map(char => {
    return charKeys.includes(char) ? chars[char] : `[${char.toUpperCase()}${char}]`;
  }).join('') + '*';
}